import { prisma } from '@/lib/prisma';
import { getSession } from '@/lib/auth';
import Link from 'next/link';
import { Plus, File, Edit, Download, ArrowRight } from 'lucide-react';
import { redirect } from 'next/navigation';

async function createTemplate() {
  'use server';
  const session = await getSession();
  if (!session) return;

  const template = await prisma.template.create({
    data: {
      name: 'Nouveau Modèle',
      content: JSON.stringify([]), // Empty elements array
      userId: session.user.id,
    },
  });
  
  redirect(`/dashboard/builder/${template.id}`);
}

export default async function DashboardPage() {
  const session = await getSession();
  const templates = await prisma.template.findMany({
    where: { userId: session.user.id },
    orderBy: { updatedAt: 'desc' },
  });

  return (
    <div className="space-y-8">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 border-b border-gray-200 pb-6">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Tableau de bord</h1>
          <p className="mt-1 text-gray-500">Gérez vos modèles de factures</p>
        </div>
        <form action={createTemplate}>
          <button type="submit" className="inline-flex items-center px-5 py-2.5 border border-transparent text-sm font-medium rounded-full shadow-lg shadow-blue-600/20 text-white bg-blue-600 hover:bg-blue-700 hover:-translate-y-0.5 transition-all">
            <Plus className="h-5 w-5 mr-2" />
            Créer un modèle
          </button>
        </form>
      </div>

      {templates.length === 0 ? (
        <div className="text-center py-16 bg-white rounded-2xl border border-dashed border-gray-300 shadow-sm">
          <div className="bg-gray-50 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
             <File className="h-8 w-8 text-gray-400" />
          </div>
          <h3 className="text-lg font-medium text-gray-900">Aucun modèle</h3>
          <p className="mt-1 text-gray-500 max-w-sm mx-auto">Vous n'avez pas encore créé de modèle de facture. Commencez dès maintenant !</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
          {templates.map((template) => (
            <div key={template.id} className="group bg-white rounded-xl border border-gray-200 shadow-sm hover:shadow-xl hover:border-blue-200 transition-all duration-300 flex flex-col overflow-hidden">
               <div className="h-40 bg-gray-50 border-b border-gray-100 flex items-center justify-center relative overflow-hidden group-hover:bg-blue-50/50 transition-colors">
                  <File className="text-gray-300 w-16 h-16 group-hover:scale-110 transition-transform duration-500" />
                  
                  {/* Overlay Actions */}
                  <div className="absolute inset-0 bg-black/0 group-hover:bg-black/5 transition-colors flex items-center justify-center opacity-0 group-hover:opacity-100 gap-3">
                      <Link href={`/dashboard/builder/${template.id}`} className="p-2 bg-white rounded-full shadow text-gray-700 hover:text-blue-600 hover:scale-110 transition-all" title="Éditer">
                        <Edit size={18} />
                      </Link>
                       <Link href={`/dashboard/generate/${template.id}`} className="p-2 bg-white rounded-full shadow text-gray-700 hover:text-green-600 hover:scale-110 transition-all" title="Générer">
                        <Download size={18} />
                      </Link>
                  </div>
               </div>
               
               <div className="p-5 flex flex-col flex-1">
                 <div className="flex-1">
                    <h3 className="text-lg font-semibold text-gray-900 group-hover:text-blue-600 transition-colors">{template.name}</h3>
                    <p className="text-sm text-gray-500 mt-1">
                      Modifié le {new Date(template.updatedAt).toLocaleDateString()}
                    </p>
                 </div>
                 
                 <div className="mt-4 pt-4 border-t border-gray-100 flex items-center justify-between">
                    <Link href={`/dashboard/builder/${template.id}`} className="text-sm font-medium text-gray-600 hover:text-blue-600 flex items-center gap-1">
                      <Edit size={14} /> Modifier
                    </Link>
                    <Link href={`/dashboard/generate/${template.id}`} className="text-sm font-medium text-blue-600 hover:text-blue-700 flex items-center gap-1 bg-blue-50 px-3 py-1 rounded-full">
                      Générer <ArrowRight size={14} />
                    </Link>
                 </div>
               </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
